import React, { useState } from 'react';
import WalletConnectProvider from "@walletconnect/client";
import { Web3ReactProvider, useWeb3React } from '@web3-react/core';

const App = () => {
  const [account, setAccount] = useState(null);
  const { activate, deactivate, active, library, account: walletAccount } = useWeb3React();

  const connectWallet = async () => {
    try {
      // Инициализация WalletConnect
      const provider = new WalletConnectProvider({
        infuraId: "7dabcdde47b6439403c2ec186b1fedaf",  // Используй свой Project ID
      });

      // Подключение к кошельку
      await provider.enable();

      // Получаем первый аккаунт
      const userAccount = provider.accounts[0];
      setAccount(userAccount);
      activate(provider);
    } catch (error) {
      console.error("Ошибка подключения:", error);
    }
  };

  const disconnectWallet = () => {
    deactivate();
    setAccount(null);
  };

  return (
    <div>
      <h1>Connect to Trust Wallet</h1>
      {!account ? (
        <button onClick={connectWallet}>Connect</button>
      ) : (
        <div>
          <p>Connected to: {account}</p>
          <button onClick={disconnectWallet}>Disconnect</button>
        </div>
      )}
    </div>
  );
};

export default App;